import PropTypes from 'prop-types';
import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

const styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
});

class TextButtonGroup extends React.Component {
    static propTypes = {
        items : PropTypes.array,
        value : PropTypes.string,
        onClick : PropTypes.func,
        type : PropTypes.string,
        classes: PropTypes.object.isRequired
    };

    render() {
      const {items,type, classes } = this.props;
        const itemElements = items.map(this.getItemElement);
        return (
            <div className={'textgroupbuttons'}>
                {itemElements}
            </div>
        );
    }

    getItemElement = (item) => {
        const {value} = this.props;
        return (
          <Button key={item.value+item.label} color="primary" onClick={this.onClick(item.value).bind(this)} className={classes.button}>
            {item.label}
          </Button>
        );
    };
    onClick(value) {
        return function () {
            this.props.onClick(value);
        };
    }
}

export default withStyles(styles)(TextButtonGroup);
